*{
    margin: 0;
    outline: none;
}

body{
    height: 100vh;
    background: linear-gradient(-45deg,#fd746c,#fdad6c);
}

nav{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
}

.logo{
    font-size: 25px;
    font-weight: bold;
    font-family: 'Acme', sans-serif;
    color: #0000006c;
}

.conatiner{
    /* background-color: #ffffff49; */
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.form{
    display: grid;
    grid-template-columns: 1fr;
    grid-gap: 10px;
    /* width: 30%; */
}

.form input{
    padding: 10px;
}

#size{
    background: transparent;
    border: none;
    border-bottom: 2px solid black;
    font-size: 20px;
    color: rgb(255, 255, 255);

}

#size::placeholder{
    color: white;
}

.convert{
    background: #00c853;
    border: none;
    color: #fff;
    font-size: 20px;
    font-weight: bold;
    box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.39);
    border-radius: 5px;
    margin-top: 20px;
}

.download{
    background: #00c853;
    text-align: center;
    color: #fff;
    background: #00c853;
    border: none;
    color: #fff;
    font-size: 20px;
    font-weight: bold;
    box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.39);
    border-radius: 5px;
    margin-top: 20px;
    padding: 10px;
    text-decoration: none;

}

.loader{
    background-color: rgba(0, 0, 0, 0.829);
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.h{
    color: rgb(132, 87, 255);
    margin-top: 30px;
}





.lds-grid {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  transform: scale(1.5);
}
.lds-grid div {
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: rgb(132, 87, 255);
  animation: lds-grid 1.2s linear infinite;
}
.lds-grid div:nth-child(1) {
  top: 8px;
  left: 8px;
  animation-delay: 0s;
}
.lds-grid div:nth-child(2) {
  top: 8px;
  left: 32px;
  animation-delay: -0.4s;
}
.lds-grid div:nth-child(3) {
  top: 8px;
  left: 56px;
  animation-delay: -0.8s;
}
.lds-grid div:nth-child(4) {
  top: 32px;
  left: 8px;
  animation-delay: -0.4s;
}
.lds-grid div:nth-child(5) {
  top: 32px;
  left: 32px;
  animation-delay: -0.8s;
}
.lds-grid div:nth-child(6) {
  top: 32px;
  left: 56px;
  animation-delay: -1.2s;
}
.lds-grid div:nth-child(7) {
  top: 56px;
  left: 8px;
  animation-delay: -0.8s;
}
.lds-grid div:nth-child(8) {
  top: 56px;
  left: 32px;
  animation-delay: -1.2s;
}
.lds-grid div:nth-child(9) {
  top: 56px;
  left: 56px;
  animation-delay: -1.6s;
}
@keyframes lds-grid {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}
